<template>
  <div>
    <div class="alert-container" v-if="alertActive">
      <vs-alert :active.sync="alertActive" :color="alertVariant" closable icon-pack="feather" close-icon="icon-x" class="mb-5">
        <p>{{ alertMessage }}</p>
      </vs-alert>
    </div>

    <nv-card>
      <div class="flex flex-wrap-reverse items-center mb-base">
        <vs-button class="p-3 mr-4" @click="cancel" color="primary" type="filled" icon="fa-chevron-left" icon-pack="far">{{ $t('BACK') }}</vs-button>
        <vs-button class="p-3 mr-4" @click="modify" color="primary" type="filled" icon="fa-pen" icon-pack="far" v-if="$acl.check('isNucleusAll')">{{ $t('MODIFY') }}</vs-button>
        <vs-button class="p-3 mr-4" @click="approve" color="primary" type="filled" icon="fa-stamp" icon-pack="far" v-if="$acl.check('isNucleusAll') && workflow.approved === 'N'">{{ $t('APPROVE') }}</vs-button>
        <vs-button class="p-3 mr-4" @click="duplicate" color="primary" type="filled" icon="fa-clone" icon-pack="far" v-if="$acl.check('isNucleusAll')">{{ $t('DUPLICATE') }}</vs-button>
        <vs-button class="p-3 mr-4" @click="designer" color="primary" type="filled" icon="fa-pencil-ruler" icon-pack="far">{{ $t('DESIGNER') }}</vs-button>
      </div>
      <div class="vs-row flex hover mb-base">
        <div class="nv-col sm:w-1/2 w-full p-2">
          <p class="info-row">
            <span>{{ $t('ID') }}: </span>
            <span>{{ workflow.workflowId }}</span>
          </p>
          <p class="info-row">
            <span>{{ $t('WORKFLOW_NAME') }}: </span>
            <span :class="disabledClass(workflow)">{{ workflow.workflowName }}</span>
          </p>
          <p class="info-row">
            <span>{{ $t('WORKFLOW_TYPE') }}: </span>
            <span>{{ workflow.workflowTypeName }}</span>
          </p>
          <p class="info-row">
            <span>{{ $t('STATUS_FLOW') }}: </span>
            <span>{{ workflow.statusFlowDescription }}</span>
          </p>
          <p class="info-row" v-if='workflow.description'>
            <span>{{ $t('DESCRIPTION') }}: </span>
            <span>{{ workflow.description }}</span>
          </p>
          <p class="info-row">
            <span>{{ $t('REMINDER_INTERVAL') }}: </span>
            <span>{{ workflow.reminderInterval }} {{ workflow.reminderIntervalDescription }}</span>
          </p>
          <p class="info-row">
            <span>{{ $t('REMINDER_LIMIT') }}: </span>
            <span>{{ workflow.reminderLimit }}</span>
          </p>
        </div>
        <div class="nv-col sm:w-1/2 w-full p-2">
          <p class="info-row">
            <span>{{ $t('INPUT_BY') }}: </span>
            <span>{{ workflow.inputUserFullName }}</span>
          </p>
          <p class="info-row" v-if="workflow.updateUserFullName">
            <span>{{ $t('UPDATED_BY') }}: </span>
            <span>{{ workflow.updateUserFullName }}</span>
          </p>
          <p class="info-row">
            <span>{{ $t('CREATION_DATE') }}: </span>
            <span>{{ workflow.creationDate | formatUnixDate }}</span>
          </p>
          <p class="info-row">
            <span>{{ $t('APPROVED') }}:</span>
            <span>{{ workflow.approved | prettyBool }}</span>
          </p>
          <p class="info-row">
            <span>{{ $t('APPROVED_BY') }}:</span>
            <span>{{ workflow.approvedBy | placeholder('-') }}</span>
          </p>
          <p class="info-row">
            <span>{{ $t('APPROVAL_DATE') }}:</span>
            <span>{{ workflow.approvalDate | formatUnixTimestamp }}</span>
          </p>
        </div>
      </div>

      <vs-tabs>
        <vs-tab :label="$t('INITIATORS')">
          <workflow-initiator @show-alert="showAlert" :workflowId="workflowId"/>
        </vs-tab>
        <vs-tab :label="$t('VARIABLES')">
          <workflow-variable @show-alert="showAlert" :workflowId="workflowId"/>
        </vs-tab>
      </vs-tabs>
    </nv-card>
  </div>
</template>

<script>
import AlertMixin from '@/mixins/commons/AlertMixin'
import DisabledClassMixin from '@/mixins/commons/DisabledClassMixin'
import ListLayoutMixin from '@/mixins/commons/ListLayoutMixin'
import WorkflowService from '@/services/commons/WorkflowService'
import WorkflowInitiator from '@/views/commons/workflow/workflow-view/WorkflowInitiator'
import WorkflowVariable from '@/views/commons/workflow/workflow-view/WorkflowVariable'

export default {
  name: 'workflow-view',
  mixins: [AlertMixin, DisabledClassMixin, ListLayoutMixin],
  components: {
    WorkflowInitiator,
    WorkflowVariable
  },
  data: function () {
    return {
      workflowId: this.$route.params.id,
      workflow: {}
    }
  },
  methods: {
    async load () {
      let self = this
      WorkflowService.workflows({
        workflowId: this.workflowId
      }).then(function (response) {
        self.workflow = response.data.workflowList[0] || {}
      })
    },
    cancel () {
      this.$router.back()
    },
    approve () {
      let self = this
      WorkflowService.approveWorkflow({
        workflowId: this.workflowId
      }).then(function (response) {
        self.showSuccessAlert(response.data.messages)
        self.load()
      }).catch(function (error) {
        self.showDangerAlert(error.response.data.errors)
      })
    },
    designer () {
      this.$router.push({ name: 'DesignWorkflow', params: { id: this.$route.params.id } })
    },
    modify () {
      this.$router.push({ name: 'ModifyWorkflow', params: { id: this.$route.params.id } })
    },
    duplicate () {
      this.$router.push({ name: 'DuplicateWorkflow', params: { id: this.$route.params.id } })
    }
  },
  mounted () {
    this.load()
  }
}
</script>
