<template>
  <div id="initiator-loading-container" class="vs-con-loading__container">
    <div class="inline-form p-4 mb-base" v-if="initiatorFormVisible">
      <div class="nv-row mb-4">
        <div class="nv-col sm:w w-full">
          <label class="vs-input--label">{{ $t('USER_GROUP') }}</label>
          <user-group-lookup :title="$t('USER_GROUP')" valueField="initiatorId" displayField="description" @select-record="selectUserGroupRecord"/>
        </div>
      </div>
      <div class="nv-row mb-4">
        <div class="nv-col sm:w-1/2 w-full">
          <label class="vs-input--label">{{ $t('FROM_DATE') }}</label>
          <nv-input-group>
            <template slot="append">
              <div class="append-text bg-dark">
                <i class="far fa-calendar-alt"></i>
              </div>
            </template>
            <datepicker class="w-full" :placeholder="$t('SELECT_DATE')" v-model="fromDate" :format="dateFormat"></datepicker>
          </nv-input-group>
        </div>
        <div class="nv-col sm:w-1/2 w-full">
          <label class="vs-input--label">{{ $t('TO_DATE') }}</label>
          <nv-input-group>
            <template slot="append">
              <div class="append-text bg-dark">
                <i class="far fa-calendar-alt"></i>
              </div>
            </template>
            <datepicker class="w-full" :placeholder="$t('SELECT_DATE')" v-model="toDate" :format="dateFormat"></datepicker>
          </nv-input-group>
        </div>
      </div>
      <div class="nv-row">
        <div class="nv-col sm:w-1/2 w-full">
          <div class="flex justify-left">
            <vs-button class="px-5 py-3 mr-2" @click="createInitiator" color="primary" type="filled">{{ $t('SUBMIT') }}</vs-button>
            <vs-button class="px-5 py-3 mr-2" @click="hideInitiatorForm" color="primary" type="border">{{ $t('CANCEL') }}</vs-button>
          </div>
        </div>
      </div>
    </div>

    <vs-table class="mb-base" :data="initiatorTableData" :noDataText="initiatorTableNoDataText" :sst="true" search @search="filterInitiatorTable" @sort="orderInitiatorTable">
      <div slot="header" class="flex flex-wrap-reverse items-center flex-grow justify-between">
        <div class="flex flex-wrap-reverse items-center">
          <vs-button class="p-3 mr-4" @click="showInitiatorForm" :disabled="initiatorFormVisible" color="primary" type="filled" icon="fa-plus" icon-pack="far">{{ $t('ADD') }}</vs-button>
          <vs-button class="p-3 mr-4" @click="refreshInitiatorTable" type="filled" icon="fa-sync" icon-pack="far" :disabled="initiatorTableBusy">{{ $t('REFRESH') }}</vs-button>
        </div>

        <div class="flex justify-end mr-4">
          <div class="p-2 border border-solid border-grey-light rounded-full d-theme-dark-bg flex items-center justify-between font-medium">
            <span class="mx-2">{{ initiatorTablePagingInfo }}</span>
            <vs-dropdown vs-trigger-click class="cursor-pointer mr-2">
              <span class="far fa-angle-down text-primary"></span>
              <vs-dropdown-menu>
                <vs-dropdown-item :key="index" v-for="(item, index) in tablePageOptions" @click="initiatorTablePerPage = item">
                  <span>{{ item }}</span>
                </vs-dropdown-item>
              </vs-dropdown-menu>
            </vs-dropdown>
          </div>
        </div>
      </div>

      <template slot="thead">
        <vs-th sort-key="initiatorId">
          {{ this.$t('ID') }}
        </vs-th>
        <vs-th sort-key="initiatorDescription">
          {{ this.$t('USER_GROUP') }}
        </vs-th>
        <vs-th sort-key="fromDate">
          {{ this.$t('FROM_DATE') }}
        </vs-th>
        <vs-th sort-key="toDate">
          {{ this.$t('TO_DATE') }}
        </vs-th>
        <vs-th>
          {{ this.$t('OPTIONS') }}
        </vs-th>
      </template>

      <template slot-scope="{data}">
        <vs-tr :key="index" v-for="(tr, index) in data" :data="tr">
          <vs-td :data="data[index].userGroupId">
            <router-link :to="{ name: 'ViewUserGroup', params: { id: data[index].userGroupId } }">{{ data[index].userGroupId }}</router-link>
          </vs-td>
          <vs-td :data="data[index].userGroupDescription">
            {{ data[index].userGroupDescription }}
          </vs-td>
          <vs-td :data="data[index].fromDate">
            {{ data[index].fromDate | formatUnixDate }}
          </vs-td>
          <vs-td :data="data[index].toDate">
            {{ data[index].toDate | formatUnixDate }}
          </vs-td>
          <vs-td>
            <div class="flex">
              <vs-button class="p-3 mr-4" color="danger" :disabled="initiatorExpired(data[index])" @click="expireInitiator(data[index].initiatorId)" icon-pack="far" icon="fa-trash-alt">{{ $t('DELETE') }}</vs-button>
            </div>
          </vs-td>
        </vs-tr>
      </template>
    </vs-table>

    <vs-pagination :total="initiatorTableTotalPages" v-model="initiatorTableCurrentPage" :max="maxPaginationItems"></vs-pagination>
  </div>
</template>

<script>
import DatePickerMixin from '@/mixins/commons/DatePickerMixin'
import LoadingMixin from '@/mixins/commons/LoadingMixin'
import ListLayoutMixin from '@/mixins/commons/ListLayoutMixin'
import WorkflowService from '@/services/commons/WorkflowService'
import UserGroupLookup from '@/views/nucleus/user-group/UserGroupLookup'
import Datepicker from 'vuejs-datepicker'

export default {
  name: 'workflow-initiator',
  mixins: [DatePickerMixin, LoadingMixin, ListLayoutMixin],
  components: {
    UserGroupLookup,
    Datepicker
  },
  props: {
    workflowId: {
      type: String,
      required: true
    }
  },
  data: function () {
    return {
      initiatorTableData: [],
      initiatorTableFilter: null,
      initiatorTableFilterTimer: null,
      initiatorTableOrderBy: null,
      initiatorTableContainer: 'initiator-loading-container',
      initiatorTableBusy: false,
      initiatorTableTotalRows: 0,
      initiatorTableCurrentPage: 1,
      initiatorTablePerPage: 10,
      initiatorFormVisible: false,
      userGroupId: '',
      fromDate: null,
      toDate: null
    }
  },
  computed: {
    initiatorTableNoDataText: function () {
      return this.initiatorTableBusy ? this.$t('LOADING') + ' ...' : this.$t('NO_DATA_FOUND')
    },
    initiatorTableTotalPages: function () {
      return Math.ceil(this.initiatorTableTotalRows / this.initiatorTablePerPage)
    },
    initiatorTablePagingInfo: function () {
      return this.pagingInfo(this.initiatorTableCurrentPage, this.initiatorTablePerPage, this.initiatorTableTotalRows)
    }
  },
  methods: {
    loadInitiatorTableData (loadingContainerId) {
      let self = this
      this.initiatorTableBusy = true
      this.initiatorTableContainer = loadingContainerId || this.initiatorTableContainer
      this.startLoading(this.initiatorTableContainer)
      WorkflowService.workflowInitiators({
        pageIndex: this.initiatorTableCurrentPage - 1,
        pageSize: this.initiatorTablePerPage,
        filter: this.initiatorTableFilter,
        orderByField: this.initiatorTableOrderBy,
        workflowId: this.workflowId
      }).then(function (response) {
        self.stopLoading(self.initiatorTableContainer)
        self.initiatorTableTotalRows = response.data.totalRows
        self.initiatorTableData = response.data.workflowInitiatorList || []
        self.initiatorTableBusy = false
      })
    },
    refreshInitiatorTable () {
      this.loadInitiatorTableData()
    },
    filterInitiatorTable (filter) {
      let self = this
      if (this.initiatorTableFilter !== filter) {
        clearTimeout(this.initiatorTableFilterTimer)
        this.initiatorTableFilterTimer = setTimeout(() => {
          self.initiatorTableFilter = filter
          self.loadInitiatorTableData()
        }, process.env.VUE_APP_SEARCH_DELAY)
      } else {
        clearTimeout(this.initiatorTableFilterTimer)
      }
    },
    orderInitiatorTable (orderBy, active) {
      this.initiatorTableOrderBy = (active ? '' : '-') + orderBy
      this.loadInitiatorTableData()
    },
    showInitiatorForm () {
      this.initiatorFormVisible = true
    },
    hideInitiatorForm () {
      this.initiatorFormVisible = false
    },
    createInitiator () {
      let self = this
      WorkflowService.createWorkflowInitiator({
        workflowId: this.workflowId,
        userGroupId: this.userGroupId,
        fromDate: this.getTime(this.fromDate),
        toDate: this.getTime(this.toDate)
      }).then(function (response) {
        self.refreshInitiatorTable()
        self.hideInitiatorForm()
      }).catch(function (error) {
        self.$emit('show-alert', 'danger', error.response.data.errors)
      })
    },
    expireInitiator (initiatorId) {
      let self = this
      WorkflowService.expireWorkflowInitiator({
        workflowId: this.workflowId,
        initiatorId: initiatorId
      }).then(function (response) {
        self.refreshInitiatorTable()
      })
    },
    selectUserGroupRecord (record) {
      this.userGroupId = record.userGroupId || ''
    },
    initiatorExpired (item) {
      if (item.toDate) {
        return item.toDate < Date.now()
      } else {
        return false
      }
    }
  },
  mounted () {
    this.loadInitiatorTableData()
  },
  watch: {
    initiatorTableCurrentPage: function () {
      this.loadInitiatorTableData()
    },
    initiatorTablePerPage: function () {
      this.loadInitiatorTableData()
    }
  }
}
</script>
