<template>
  <div id="variable-loading-container" class="vs-con-loading__container">
    <div class="inline-form p-4 mb-base" v-if="variableFormVisible">
      <div class="nv-row mb-4">
        <div class="nv-col sm:w-1/2 w-full required">
          <label class="vs-input--label">{{ $t('VARIABLE_NAME') }}</label>
          <vs-input class="w-full" v-model="variableName"/>
        </div>
        <div class="nv-col sm:w-1/2 w-full required">
          <label class="vs-input--label">{{ $t('VARIABLE_TYPE') }}</label>
          <vs-select autocomplete class="w-full" :placeholder="$t('SELECT_VALUE')" v-model="variableTypeEnumId">
            <vs-select-item v-for="(variableType, index) in variableTypes" :key="index" :value="variableType.enumId" :text="variableType.description"/>
          </vs-select>
        </div>
      </div>
      <div class="nv-row mb-4">
        <div class="nv-col w-full">
          <label class="vs-input--label">{{ $t('DEFAULT_VALUE') }}</label>
          <vs-input v-if="variableTypeEnumId === 'WF_VAR_TEXT'" class="w-full" v-model="defaultValue"/>
          <vs-input v-if="variableTypeEnumId === 'WF_VAR_NUMBER'" type="number" class="w-full" v-model="defaultValue"/>
          <nv-input-group v-if="variableTypeEnumId === 'WF_VAR_DATE'">
            <template slot="append">
              <div class="append-text bg-dark">
                <i class="far fa-calendar-alt"></i>
              </div>
            </template>
            <datepicker class="w-full" :placeholder="$t('SELECT_DATE')" v-model="defaultValue" :format="dateFormat"></datepicker>
          </nv-input-group>
          <vs-switch v-if="variableTypeEnumId === 'WF_VAR_BOOLEAN'" v-model="defaultValue" class="mt-2"/>
        </div>
      </div>
      <div class="nv-row">
        <div class="nv-col sm:w-1/2 w-full">
          <div class="flex justify-left">
            <vs-button class="px-5 py-3 mr-2" @click="createWorkflowVariable" color="primary" type="filled">{{ $t('SUBMIT') }}</vs-button>
            <vs-button class="px-5 py-3 mr-2" @click="hideVariableForm" color="primary" type="border">{{ $t('CANCEL') }}</vs-button>
          </div>
        </div>
      </div>
    </div>

    <vs-table class="mb-base" :data="variableTableData" :noDataText="variableTableNoDataText" :sst="true" search @search="filterVariableTable" @sort="orderVariableTable">
      <div slot="header" class="flex flex-wrap-reverse items-center flex-grow justify-between">
        <div class="flex flex-wrap-reverse items-center">
          <vs-button class="p-3 mr-4" @click="showVariableForm" :disabled="variableFormVisible" color="primary" type="filled" icon="fa-plus" icon-pack="far">{{ $t('ADD') }}</vs-button>
          <vs-button class="p-3 mr-4" @click="refreshVariableTable" type="filled" icon="fa-sync" icon-pack="far" :disabled="variableTableBusy">{{ $t('REFRESH') }}</vs-button>
        </div>

        <div class="flex justify-end mr-4">
          <div class="p-2 border border-solid border-grey-light rounded-full d-theme-dark-bg flex items-center justify-between font-medium">
            <span class="mx-2">{{ variableTablePagingInfo }}</span>
            <vs-dropdown vs-trigger-click class="cursor-pointer mr-2">
              <span class="far fa-angle-down text-primary"></span>
              <vs-dropdown-menu>
                <vs-dropdown-item :key="index" v-for="(item, index) in tablePageOptions" @click="variableTablePerPage = item">
                  <span>{{ item }}</span>
                </vs-dropdown-item>
              </vs-dropdown-menu>
            </vs-dropdown>
          </div>
        </div>
      </div>

      <template slot="thead">
        <vs-th sort-key="variableName">
          {{ this.$t('VARIABLE_NAME') }}
        </vs-th>
        <vs-th sort-key="variableTypeDescription">
          {{ this.$t('VARIABLE_TYPE') }}
        </vs-th>
        <vs-th sort-key="defaultValue">
          {{ this.$t('DEFAULT_VALUE') }}
        </vs-th>
      </template>

      <template slot-scope="{data}">
        <vs-tr :key="index" v-for="(tr, index) in data" :data="tr">
          <vs-td :data="data[index].variableName">
            {{ data[index].variableName }}
          </vs-td>
          <vs-td :data="data[index].variableTypeDescription">
            {{ data[index].variableTypeDescription | placeholder('-') }}
          </vs-td>
          <vs-td :data="data[index].defaultValue">
            {{ data[index].defaultValue | placeholder('-') }}
          </vs-td>
        </vs-tr>
      </template>
    </vs-table>

    <vs-pagination :total="variableTableTotalPages" v-model="variableTableCurrentPage" :max="maxPaginationItems"></vs-pagination>
  </div>
</template>

<script>
import DatePickerMixin from '@/mixins/commons/DatePickerMixin'
import LoadingMixin from '@/mixins/commons/LoadingMixin'
import ListLayoutMixin from '@/mixins/commons/ListLayoutMixin'
import EnumerationService from '@/services/commons/EnumerationService'
import WorkflowService from '@/services/commons/WorkflowService'
import Datepicker from 'vuejs-datepicker'

export default {
  name: 'workflow-variable',
  mixins: [DatePickerMixin, LoadingMixin, ListLayoutMixin],
  components: {
    Datepicker
  },
  props: {
    workflowId: {
      type: String,
      required: true
    }
  },
  data: function () {
    return {
      variableTableData: [],
      variableTableFilter: null,
      variableTableFilterTimer: null,
      variableTableOrderBy: null,
      variableTableContainer: 'variable-loading-container',
      variableTableBusy: false,
      variableTableTotalRows: 0,
      variableTableCurrentPage: 1,
      variableTablePerPage: 10,
      variableFormVisible: false,
      variableName: '',
      variableTypes: [],
      variableTypeEnumId: 'WF_VAR_TEXT',
      description: '',
      defaultValue: ''
    }
  },
  computed: {
    variableTableNoDataText: function () {
      return this.variableTableBusy ? this.$t('LOADING') + ' ...' : this.$t('NO_DATA_FOUND')
    },
    variableTableTotalPages: function () {
      return Math.ceil(this.variableTableTotalRows / this.variableTablePerPage)
    },
    variableTablePagingInfo: function () {
      return this.pagingInfo(this.variableTableCurrentPage, this.variableTablePerPage, this.variableTableTotalRows)
    }
  },
  methods: {
    async load () {
      let self = this
      EnumerationService.enumerations({
        enumTypeId: 'WorkflowVariableType',
        orderByField: 'sequenceNum,description'
      }).then(function (response) {
        self.variableTypes = response.data.enumerationList || []
      })
    },
    loadVariableTableData (loadingContainerId) {
      let self = this
      this.variableTableBusy = true
      this.variableTableContainer = loadingContainerId || this.variableTableContainer
      this.startLoading(this.variableTableContainer)
      WorkflowService.workflowVariables({
        pageIndex: this.variableTableCurrentPage - 1,
        pageSize: this.variableTablePerPage,
        filter: this.variableTableFilter,
        orderByField: this.variableTableOrderBy,
        workflowId: this.workflowId
      }).then(function (response) {
        self.stopLoading(self.variableTableContainer)
        self.variableTableTotalRows = response.data.totalRows
        self.variableTableData = response.data.workflowVariableList || []
        self.variableTableBusy = false
      })
    },
    refreshVariables () {
      this.refreshVariableTable()
      this.variableName = ''
      this.defaultValue = ''
      this.description = ''
    },
    filterVariableTable (filter) {
      let self = this
      if (this.variableTableFilter !== filter) {
        clearTimeout(this.variableTableFilterTimer)
        this.variableTableFilterTimer = setTimeout(() => {
          self.variableTableFilter = filter
          self.loadVariableTableData()
        }, process.env.VUE_APP_SEARCH_DELAY)
      } else {
        clearTimeout(this.variableTableFilterTimer)
      }
    },
    orderVariableTable (orderBy, active) {
      this.variableTableOrderBy = (active ? '' : '-') + orderBy
      this.loadVariableTableData()
    },
    refreshVariableTable () {
      this.loadVariableTableData()
    },
    showVariableForm () {
      this.variableFormVisible = true
    },
    hideVariableForm () {
      this.variableFormVisible = false
    },
    createWorkflowVariable () {
      let self = this
      WorkflowService.createWorkflowVariable({
        workflowId: this.workflowId,
        variableName: this.variableName,
        description: this.description,
        variableTypeEnumId: this.variableTypeEnumId,
        defaultValue: this.defaultValue
      }).then(function (response) {
        self.refreshVariables()
        self.hideVariableForm()
      }).catch(function (error) {
        self.$emit('show-alert', 'danger', error.response.data.errors)
      })
    }
  },
  mounted () {
    this.load()
    this.loadVariableTableData()
  },
  watch: {
    variableTableCurrentPage: function () {
      this.loadVariableTableData()
    },
    variableTablePerPage: function () {
      this.loadVariableTableData()
    }
  }
}
</script>
